
.header-color{
  /*import background color from uigradients.com */
  background: #2c3e50;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #3498db, #2c3e50);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #3498db, #2c3e50); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.landing-grid{
    height: 100%;
    background: url('../src/cup-desk-drink-434337.jpg');
    background-repeat: no-repeat;
    background-size:cover;
}

.avatar-image{
  content:url('../src/profile_new.jpg');
  display: block;
margin-left: auto;
margin-right: auto;
width: 8%;
border-radius: 2em;
}


.banner-text{
  background-color: black;
  opacity: .7;
  width:75%;
  height: auto;
  margin:auto;
  border-radius:1em;
}

.banner-text h1{
  font-size: 2em;
  font-weight:bold;
  color:white;
  padding-top: 0.5em;
  text-align: center;
  margin-bottom: auto;
}

.banner-text hr{
  border-top: 0.3em dotted white;
  width:50%;
  margin:auto;
}

.banner-text p{
  color:white;
  font-size: 1em;
  padding:1em;
  text-align: center;
  margin-bottom: 0.2em;
}

.banner-text .aboutme{
  font-size: 1.3em;
}

.button-links{
  margin:auto;
  display: flex;
}

.social-links{
  justify-content: space-between;
  text-align: center;
  color:white;
  margin:auto;
}

.social-links i{
  color:white;
  font-size: 5em;
  padding: 1%;
  margin: 1%;
  border-radius: 10em;
  -webkit-transition-duration: 0.4s;
  transition-duration: 0.4s;
}
.social-links i:hover{
  color:black;
  font-size: 5em;
  padding: 1%;
  background-color: white;
}

.landing-grid .mdl-button.mdl-button--colored {
    color: rgb(255, 255, 255);
    background: #004a96;
    font-size: 1.5em;
    text-transform: none;
    font-weight: bold;
    padding: 1%;
    display: block;
    margin:auto;
    border-radius: 0.2em;
    font-family: 'Oxygen', sans-serif;
    -webkit-transition-duration: 0.4s;
    transition-duration: 0.4s;
    float: left;
    margin: auto;
    width: 20%;
}


.landing-grid .mdl-button.mdl-button--colored:hover{
  background-color: #4CAF50; /* Green */
  color: black;
}
.landing-grid .button-links .view-projects {
    background-color: rgb(222, 102, 0);
}

.cardtitle-1{
  color:black;
  height:176px;
  background:url(../src/cup-desk-drink-434337.jpg);
}

.projects-grid{
  display: flex;
}

/* contact page css*/

.contact-body{
  margin:auto;
  position: fixed;
  width: 100%;
  height:100%;
  justify-content:center;
  padding-top:2em;
}

.contact-grid{
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2{
  font-family: 'Anton', sans-serif;
}

.contact-grid p{
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr{
  border-top: 5px dotted black;
  width: 50%;
  margin:auto;
}

.contact-list i{
  font-size: 50px;
  padding-right: 5rem;
}

/* Resume page css*/
.resume-left .resume-image {
      content: url(../src/profile_new.jpg);
      margin: auto;
      width: 5%;
      border-radius: 2em;
}


.resume-right{
  background: rgba(0, 0, 0, 0.2);
  color: black;
  padding: 2em;
}

.resume-right hr{
  border-top: 0.1em dotted white;
  opacity: .6;
  margin: auto;
}

/* Contact page css*/

.contact-grid .contact-image {
      content: url(../src/profile_new.jpg);
      margin: auto;
      width: 5%;
      border-radius: 2em;
}
